html {
    background-color: #9ba5b6;
}

.gesture-area {
    position:relative;
    z-index: 0;
}

.scale-element {
    position: absolute;
    display: block;
    max-width: 100%;
    margin: 0;
    touch-action: none;
    width: 200px;
}

#selected-img {
    border: 3px solid cyan;
}